export default {
	data() {
		return {
			datas: ["关于商城", "意见反馈", "协议规则", "资质证件", "用户协议", "隐私政策"]
		}
	},
	methods: {
		returni(){
		uni.navigateBack({
			delta:1
		})
		},
		//个人资料
		personal() {
			uni.navigateTo({
				url: "../personal/personal?id=1"
			})
		},
		//收货地址
		receiving() {
			uni.navigateTo({
				url:"../address/address?id=1"
			})
		},
		sign(){
		uni.showToast({
			title: '点击协',
			icon: 'none'
		})
		},
		navTop(index) {
			switch (index) {
				case 0: //关于商城
					uni.navigateTo({
						url: "/pages/mine/article/article?id=1"
					})
					break;
				case 1: //意见反馈
					uni.navigateTo({
						url: "/pages/mine/feedback/feedback?id=1"
					})
					break;
				case 2: //协议规则
					uni.showToast({
						title: '点击协议规则',
						icon: 'none'
					})
					break;
				case 3: //资质证件
					uni.showToast({
						title: '点击资质证件',
						icon: 'none'
					})
					break;
				case 4: //用户协议
					uni.showToast({
						title: '点击用户协议',
						icon: 'none'
					})
					break;
				case 5: //隐私政策
					uni.showToast({
						title: '点击隐私政策',
						icon: 'none'
					})
					break;
				default:
					break;
			}
		},
	}
}
